<template>
  <div id="detail">
    <detail-nav-bar
      class="detail-nav-bar"
      @titleClick="titleClick($event)"
      ref="nav"
    />
    <div
      class="detailContent"
      ref="detailContent"
      @scroll="contentScroll($event)"
    >
      <detail-swiper :top-images="topImages"></detail-swiper>
      <detail-base-info :goods="goods" />
      <detail-shop-info :shop="shop" />
      <detail-goods-info :detail-info="detailInfo" @imageLoad="imageLoad()" />
      <detail-param-info :param-info="goodsParam" ref="param" />
      <!-- 属性名：使用 xx-yy 的形式 -->
      <detail-comment-info :comment-info="commentInfo" ref="comment" />
      <goods-list :goods="recommends" ref="recommend" />
    </div>
    <detail-bottom-bar class="bottom-bar" @addCart="addToCart()" />
    <back-top @click.native="backTop()" v-show="isShowBackTop" />
  </div>
</template>

<script>
// Deatil子组件
import DetailNavBar from "./childComps/DetailNavBar.vue";
import DetailSwiper from "./childComps/DetailSwiper.vue";
import DetailBaseInfo from "./childComps/DetailBaseInfo.vue";
import DetailShopInfo from "./childComps/DetailShopInfo.vue";
import DetailGoodsInfo from "./childComps/DetailGoodsInfo.vue";
import DetailParamInfo from "./childComps/DetailParamsInfo.vue";
import DetailCommentInfo from "./childComps/DetailCommentInfo.vue";
import DetailBottomBar from "./childComps/DeatilBottomBar.vue";
// 推荐复用主页组件
import GoodsList from "components/content/goods/GoodsList.vue";
// 混入
import { backTopMixin } from "common/mixin";
// 网络请求，数据结构类
import {
  getDetail,
  getRecommends,
  Goods,
  Shop,
  GoodsParam,
} from "network/detail";
// 工具类，防抖
import { _debounce } from "common/utils";
// Vuex，action
import { mapActions } from "vuex";
// toast
export default {
  name: "Detail",
  data() {
    return {
      iid: "",
      topImages: [],
      // 数据在父组件，做好分离、整合；子组件开箱即用
      goods: {},
      shop: {},
      detailInfo: {},
      goodsParam: {},
      commentInfo: {},
      recommends: [],
      themeTopYs: [0],
      currentIndex: 0,
    };
  },
  created() {
    // 属性名，取决于路由配置
    this.iid = this.$route.params.iid;
    // 网络请求
    getDetail(this.iid).then((res) => {
      const result = res.result;

      // 1.轮播图
      this.topImages = result.itemInfo.topImages;
      // 2.商品信息
      this.goods = new Goods(
        result.itemInfo,
        result.columns,
        result.shopInfo.services
      );
      // 3.店铺信息
      this.shop = new Shop(result.shopInfo);
      // 4.详情信息
      this.detailInfo = result.detailInfo;
      // 5.参数信息
      this.goodsParam = new GoodsParam(
        result.itemParams.info,
        result.itemParams.rule
      );
      if (result.rate.cRate > 0) {
        this.commentInfo = result.rate.list[0];
      }
    });

    getRecommends().then((res) => {
      this.recommends = res.data.list;
    });
    // this.$nextTick(() => {
    // 图片高度还未计算在内
    // });
    this.getThemeTopY = _debounce(() => {
      this.themeTopYs = [];
      this.themeTopYs.push(0);
      this.themeTopYs.push(this.$refs.param.$el.offsetTop - 44);
      this.themeTopYs.push(this.$refs.comment.$el.offsetTop - 44);
      this.themeTopYs.push(this.$refs.recommend.$el.offsetTop - 44);
      this.themeTopYs.push(Number.MAX_VALUE);
    }, 100);
  },
  mounted() {},
  mixins: [backTopMixin],
  methods: {
    imageLoad() {
      this.getThemeTopY();
    },
    titleClick(index) {
      this.$refs.detailContent.scrollTo(0, this.themeTopYs[index], {
        behavior: "smooth",
      });
    },
    contentScroll(position) {
      const detailContentElement = this.$refs.detailContent;
      for (let i = 0; i < this.themeTopYs.length - 1; i++) {
        if (this.currentIndex === i) {
          continue;
        }
        if (
          detailContentElement.scrollTop >= this.themeTopYs[i] &&
          detailContentElement.scrollTop < this.themeTopYs[i + 1]
        ) {
          this.currentIndex = i;
          this.$refs.nav.currentTitle = this.currentIndex;
        }
      }
      this.isShowBackTop = this.$refs.detailContent.scrollTop >= 50;
    },
    backTop() {
      this.$refs.detailContent.scrollTo(0, 0, {
        behavior: "smooth",
      });
    },
    ...mapActions({
      addCart_action: "addCart",
    }),
    addToCart() {
      let product = {};
      product.image = this.topImages[0];
      product.title = this.goods.title;
      product.desc = this.goods.desc;
      product.price = this.goods.nowPrice;
      product.iid = this.iid;
      this.addCart_action(product).then((res) => {
        this.$toast.show(res);
      });
    },
  },
  components: {
    DetailNavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    DetailCommentInfo,
    DetailBottomBar,
    GoodsList,
  },
};
</script>

<style scoped>
#detail {
  background-color: #f4f4f4;
  position: relative;
  z-index: 12;
}
.detailContent {
  overflow-y: scroll;
  height: calc(100vh - 6.59670vh - 7.34632vh);
  scroll-behavior: smooth;
}
.detail-nav-bar {
  background-color: #fff;
  position: relative;
  z-index: 12;
}
.bottom-bar {
  position: fixed;
  bottom: 0;
}
</style>